.productForm {
  margin: 20px auto;
  margin-left: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  width: 600px;
}

.text {
  font-size: 20px;
  text-align: left;
}

.input {
  border-radius: 5px;
  border: 1px solid;
  background-color: rgb(255, 255, 255);
  font-size: 20px;
  width: 300px;
}
